* {
  box-sizing: border-box;
}

html {
  --bg: #fff;
  --fg: #25282A;
  --gray: #888;
  --royal: #0072CE;
  --blue: #6AD1E3;
  --primary: #710581;
  --secondary: var(--royal);
  --tertiary: #FF585D;

  background-color: var(--bg);
  background-image: linear-gradient(234deg, #f7fbf7 50%, #e1b8dc);
  min-height: 100vh;

  font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica,Arial,sans-serif;
  font-size: calc(.65em + 0.7vw);
  line-height: 1.3;

  ::selection {
    background: var(--secondary);
    color: var(--bg);
  }

  @media (prefers-color-scheme: dark) {
    --bg: #25282A;
    --fg: #fff;
    --secondary: var(--blue);

    ::selection {
      background: var(--secondary);
      color: var(--fg);
    }
  }
}

body {
  margin: 0 auto;
  padding: 0 1em;
  max-width: 40em;
}

fieldset {
  border: none;
  margin: 0;
  padding: 0;
}

.highlight {
  align-items: center;
  display: flex;
  margin-bottom: 0.5em;
  width: 100%;
  label {
    margin-right: 0.5em;
  }
  input {
    caret-color: var(--secondary);
  }
}

label {
  color: var(--gray);
}

button, .highlight {
  border-radius: 5px;
  border-color: var(--primary);
  border: 0.1em solid var(--primary);
  padding: 0.5em 1em;

  &:hover, &:focus, &:focus-within {
    border-color: var(--secondary);
  }
}

input {
  border: none;
  flex: 1;
  &:read-only {
    color: var(--primary)
  }
}

input[type="number"] {
  text-align: center;
  border-bottom: 0.1em solid var(--primary);
  margin: 0 1em;
  width: 4em;
  padding-left: 0.5em;
  &:hover, &:focus {
    border-color: var(--secondary);
  }
}

button, input {
  background: transparent;
  color: inherit;
  cursor: pointer;
  font: inherit;
  outline: none;
}
a {
  cursor: pointer;
}

button {
  position: relative;
  transition: top 50ms;
  &:hover, &:focus {
    border-color: transparent;
  }
  &:active {
    background: var(--secondary);
    border-color: var(--secondary);
    top: 1px;
  }
}
.is-premium {
  border-left: 0.25em solid var(--secondary);
  padding-left: 0.25em;
  margin-left: -0.5em;
}
.main {                                 /* add space for header */
  margin-top: 120px;
}
.header {                               /* Container */
  width: 100vw;
  position: absolute;
  left: 0px;
  top: 0px;
  padding: 0px 30px;
  margin: 30px 0;
}
.header h1 {                            /* Logo with text */
  margin: 0px;
  padding-left: 60px;
  position: absolute
}
.header .logo {
  position: absolute;
  font-size: 45px;
  padding-top: 5px;
}
.topMenu{                               /* Center menu buttons */
  float: right;
  position: relative;
  display: table;
  width: 40%;
  margin: 15px 30%;
  table-layout: fixed;
  border-spacing: 2px;
}
.topMenu .item {
  display: table-cell;
}
.topMenu a{
  color: var(--primary);
  padding: 12px;
  border-radius: 10px;
}
.topMenu a:hover {
 background-color:  var(--primary);
 color: var(--fg);
}

header button {                         /* wallet button */
  position: absolute;
  top: 30px;
  right: 30px;
  background: var(--primary);
  color: var(--fg);
  border-radius: 20px;
}

fieldset, .elementBox {                              /* Order container */
  border: 1px solid var(--primary);
  border-radius: 25px;
  padding: 20px;
  margin: 20px;
  background: var(--fg);
}
.elementBox {
  display: flex
}
.elementBox .selection{
  flex: 4;
}
.elementBox .price{
  margin-top: 40px;
  flex: 2;
  color: var(--primary);
  position: relative;
  right: 20px;
  text-align: right;
}
.elementBox h3{
  color: var(--primary);
  margin: 10px 0;
}
.elementBox p {
  color: var(--primary);
}
.downArrow {
  border-radius: 100px;
  background: var(--primary);
  position: absolute;
  right: calc(50% - 30px);
  height: 60px;
  width: 60px;
  margin-top: -60px;
}
.modeMenu{                                          /* in element menu buttons */
  color: var(--primary);
  display: table;
  table-layout: fixed;
  border-spacing: 30px;
  padding: 15px;

}
.modeMenu a{
  padding: 2px;
  display: table-cell;
  font-size: 20px;
  font-weight: 400;
}
.modeMenu a.active{
  font-size: 30px;
  font-weight: 600;
}
.elementButton button{                                /* Compleate button in same style as element */
  width: -webkit-fill-available;
  padding: 20px;
  margin: 20px;
  background-image: linear-gradient(37deg, #dcb5f3, #fbd1b9);
  border: 1px solid var(--primary);
  border-radius: 19px;
}


/* Dropdown Button */
.dropbtn {
  background-color: transparent;
  border-radius: 19px;
  color: var(--primary);
  border: 1px solid var(--primary);
  padding: 16px;
  font-size: 16px;
}

/* The container <div> - needed to position the dropdown content */
.dropdown {
  position: relative;
  display: inline-block;
  border-radius: 19px;
}

/* Dropdown Content (Hidden by Default) */
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
  border: 1px solid var(--primary);
  border-radius: 19px;
}

/* Links inside the dropdown */
.dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  border-radius: 19px;
}

/* Change color of dropdown links on hover */
.dropdown-content a:hover {background-color: var(--primary); color: var(--fg);}

/* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content {display: block;}

/* Change the background color of the dropdown button when the dropdown content is shown */
.dropdown:hover .dropbtn {background-color: var(--primary); color: var(--fg);}
